/*--------------invest--------------*/

.slide-card {
  background: var(--slideCard);
  border-radius: 10px;
  padding: 16px;
  margin: 15px;
  text-align: center;
}

.slide-card-lite {
  background: #f7f7f7;
  border-radius: 10px;
  padding: 16px;
  margin: 15px;
  text-align: center;
}

/*--------------input-btn--------------*/

.btn-input {
  border: 2px solid var(--divider) !important;
  border-radius: 10px !important;
  align-items: center;
  width: 90%;
  margin-left: 20px;
}

.underLineBlue{
  color: var(--brandSecondary);
  border-bottom: 1px dotted var(--brandSecondary);
}


/* -----------------bottom drawer---------------------- */
.bottom-drawer .modal-dialog{
  width: 100%;
  margin: 0;
  position: absolute;
  bottom: 50px;
}
.modal.fade .modal-dialog{
  transform: translate(0,50px) !important
}

.bottom-drawer .modal-footer{
  display: flex;
  margin: 0;
  gap: 10px;
  padding: 4%;
  border-top: 1px solid #e5e5e5;
}
.bottom-drawer .modal-footer button{
  flex:1;
  height: 44px;
  border: 1px solid #00a6a4;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 500;
  margin: 0;
}
.bottom-drawer .modal-footer button.close{
  background: #fff;
  color:#00a6a4;
}
.bottom-drawer .modal-footer button.confirm{
  background: #00a6a4;
  color:#fff;
}
.bottom-drawer  input[type="radio"]{
  margin-right: 10px;
}


label.radio{
  display: block;
  margin-bottom: 6%;
  font-size: 14px;
  font-weight: 400;
  color: #333333;
}

label.radio input[type="radio"]{
  margin-right: 4%;
}

.normalInput{
  outline:none;
  border:none;
  border-bottom:1px solid #e5e5e5;
  margin-top:2%;
  margin-bottom:4%;
  font-size:24px;
  font-weight:500;
  width:100%
}

.normalInput::placeholder{
  font-size: 24px;
  font-weight: 500;
  color: #DDDDDD;
}

